(function () {
    'use strict';
    var angular = require('angular');
    require('./negative-company-application.component.scss');

    var negativeCompany = angular.module('app').component('negativeCompany', {
        template: require('./negative-company-application.component.html'),
        controller: ['analyticService', 'appState', function (analyticService, appState) {
            var vm = this;
            vm.$onInit = function () {
                analyticService.trackPageLoad(analyticService.pageList.negativeCompany);
                appState.clearAll();
            };
            vm.exit = function () {
                analyticService.buttonList.buttonClick.event_content = "exit";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                window.location.href = "http://www.hsbc.co.in/"
            }
        }]
    });

    module.exports = negativeCompany.name;
})();



